.preview {
	background-image: url('../../images/main.png');
	background-repeat: no-repeat;
	background-size: 100%;
	width: 100%;

	height: 920px;
	display: flex;
	margin-top: 10px;
	/* position: absolute;
	top: 20px;
	left: 0;
	z-index: -1; */
}
.preview__figure {
	background-image: url('../../images/main-figure.svg');
	background-repeat: no-repeat;
	background-size: 100%;
	width: 100%;
	height: 450px;
	position: absolute;
	top: 500px;
}
.preview__info {
	padding: 190px 0 0 91.5px;
}
.preview__title {
	margin: 0;
	padding: 0 0 5px 0;
	max-width: 900px;

	font-family: 'NeueMachina', sans-serif, 'Courier New', Courier, monospace;
	font-style: normal;
	font-weight: 800;
	font-size: 62px;
	line-height: 143%;
	text-transform: uppercase;
	color: #ffffff;
}
.preview__subtitle {
	margin: 0;
	padding: 0;
	max-width: 580px;

	font-family: 'Montserrat', sans-serif, 'Courier New', Courier, monospace;
	font-style: normal;
	font-weight: 500;
	font-size: 16px;
	line-height: 143%;
	color: #ffffff;
}
.preview__bits {
	background-image: url('../../images/bit-preview.png');
	background-repeat: no-repeat;
	height: 750px;
	margin-left: -10px;
}
@media screen and (max-width: 1341px) {
	.preview__info {
	}
	.preview {
		border-radius: 68px;
		margin-bottom: 80px;
		background-size: cover;
		height: 600px;
	}
	.preview__bits {
		display: none;
	}
	.preview__figure {
		display: none;
	}
}
@media screen and (max-width: 1100px) {
	.preview {
		background-position: -150px;
	}
	.preview__info {
		padding: 150px 0 0 51px;
	}
	.preview__title {
		font-size: 46px;
		max-width: 550px;
	}
	.preview__subtitle {
		font-size: 15px;
		max-width: 400px;
	}
}
@media screen and (max-width: 950px) {
	.preview {
		background-position: -250px;
	}
}

@media screen and (max-width: 767px) {
	.preview {
		background-position: 0;
		background-size: 100%;
		border-radius: 0;
		margin-top: 30px;
		width: 100%;
		max-width: 767px;
		height: 255px;
		background-image: url('../../images/preview-mobile.png');
	}
	.preview__figure {
		display: block;
		height: 155px;
		top: 180px;
		left: 0;
		background-image: url('../../images/figure-mobile.png');
	}
	.preview__title {
		display: none;
	}
	.preview__subtitle {
		display: none;
	}
	.preview__bits {
		display: none;
	}
}
